.gatsbyHighlight {
    background: rgb(40, 44, 52);
    color: rgb(255, 255, 255);
    border-radius: 8px;
    overflow: auto;
}

.gatsbyCode {
    margin: 1rem;
    font-size: 13px;
    line-height: 23px;
}

.gatsbyText  {
    margin-left: 1rem;
    user-select: text;
    white-space: pre-wrap;
    word-break: break-word;
}
.gatsbyText,
.gatsbyText .token,  
.gatsbyText .tokenNpm  {
    font-size: 14px;
}

.token {
    color: rgb(121, 182, 242);
    user-select: text;
    
}
.tokenNpm {
    color: rgb(29, 228, 11);
    user-select: text;
}

.containerImg {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.img_wrapper {
    border-radius: 8px;
}

@media (max-width: 714px) {
    .containerImg {
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .text {
        width: 100%;
    }
}