.modal {
    margin: 0 auto;
    width: 100%;
    display: none;
    cursor: pointer;
    border: 1px solid;
    border-radius: 16px;
}
.containerImg {
    overflow: hidden;
}

.growDown {
    display: block;
    animation: growDown 300ms ease-in-out forwards;
}

@keyframes growDown {
    0% {
        transform: translateY(-100%);
    }
    100% {
       transform: translateY(0px);
    }
}

.downOut {
    display: block;
    animation: downOut 300ms ease-in-out forwards
}

@keyframes downOut {
    0% {
        transform: translateY(0px);
    }
    100% {
       transform: translateY(-100%);
       display: none;
    }
}
.btnColor {
    color:blue;
    cursor: pointer;
}